/* LoginPage.css */
.LoginPage {
    padding-top: 5%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f0f0f0;
}

.LoginPage h4 {
  text-align: center;
  font-family: 'Arial', sans-serif;
  font-weight: bold;
  font-style: italic; 
}

.SubmitButton {
  width: 100%;
  padding: 10px;
  background-color: #414bb2 !important;
  color: white !important;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.logo{
  max-width: 30%;
  height: auto;
  display: block;
  border-radius: 50%;
  margin: 0 auto;
}