/* Estilos para TripPage */
.TripPageContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5%;
  }
  
h1 {
  font-size: 24px;
  margin-bottom: 10px;
}

/* Estilos para la galería de imágenes con CSS Grid (vertical) */
.ImageGallery {
  display: grid;
  grid-template-rows: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 10px;
  justify-items: center;
}

.ImageContainer {
  height: 100%;
}

.ImageContainer img {
  max-height: 100%;
  width: auto;
  border: 2px solid #ccc;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}

.google-map{
  position: absolute;
  top: 300px;
  left: 0px;
  right: 50px;
  width: 100%;
  height: 55%;
}

.DestinationDetails {
  background-color: whitesmoke;
  position: relative;
  z-index: 999;
  width: fit-content;
  padding: 2%;
  top: 50%;
  left: 50%;
}