.Container {
    display: flex;
    background-color: #0471a6;
    flex-direction: column;

}

.Group {
    width: 100%;
    height: 50px;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 33px;
    margin-left: 24px;
    display: flex;
    position: fixed;
}

.BottomNavigation {
    color: #0471a6 !important;
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 0;
}

.selected {
    color: #414bb2 !important; /* Cambia el color a tu elección */
    background-color: gray !important;
}